<script lang="ts">;
;
;
;
;
;
import { map_aria_attributes, map_attributes, map_data_attributes, map_global_attributes, } from "../../../util/attributes";
export let element = undefined;
export let active = undefined;
export let disabled = undefined;
export let type = undefined;
export let value = "";
export let download = "";
export let href = "";
export let rel = "";
export let target = "";
let _for = undefined;
export { _for as for };
export let palette = undefined;
export let size = undefined;
export let variation = undefined;
</script>

{#if href}
    <a
        bind:this={element}
        {...map_global_attributes($$props)}
        role="button"
        {...map_data_attributes({palette, size, variation})}
        {...map_aria_attributes({active, disabled})}
        {...map_attributes({download, href, rel, target})}
        on:click
    >
        <slot />
    </a>
{:else if _for}
    {#if _for === true}
        <label
            bind:this={element}
            {...map_global_attributes($$props)}
            role="button"
            {...map_data_attributes({palette, size, variation})}
            {...map_aria_attributes({disabled, pressed: active})}
            on:click
        >
            <slot />
        </label>
    {:else}
        <label
            bind:this={element}
            {...map_global_attributes($$props)}
            role="button"
            for={_for}
            {...map_data_attributes({palette, size, variation})}
            {...map_aria_attributes({disabled, pressed: active})}
            on:click
        >
            <slot />
        </label>
    {/if}
{:else if value}
    {#if type === "reset"}
        <input
            bind:this={element}
            {...map_global_attributes($$props)}
            type="reset"
            {...map_data_attributes({palette, size, variation})}
            {...map_aria_attributes({pressed: active})}
            {...map_attributes({disabled, value})}
            on:click
        />
    {:else if type === "submit"}
        <input
            bind:this={element}
            {...map_global_attributes($$props)}
            type="submit"
            {...map_data_attributes({palette, size, variation})}
            {...map_aria_attributes({pressed: active})}
            {...map_attributes({disabled, value})}
            on:click
        />
    {:else}
        <input
            bind:this={element}
            {...map_global_attributes($$props)}
            type="button"
            {...map_data_attributes({palette, size, variation})}
            {...map_aria_attributes({pressed: active})}
            {...map_attributes({disabled, value})}
            on:click
        />
    {/if}
{:else}
    <button
        bind:this={element}
        {...map_global_attributes($$props)}
        {...map_data_attributes({palette, size, variation})}
        {...map_aria_attributes({pressed: active})}
        {...map_attributes({disabled})}
        on:click
    >
        <slot />
    </button>
{/if}
